import * as React from "react";
import { Fade, Bounce } from "react-awesome-reveal";
import { Nav } from "../Nav/Nav";

export const Go = () => {
  return (
    <div>
      <Nav />
      <article className="works__bg">
        <img
          src={process.env.PUBLIC_URL + "/assets/images/ls.png"}
          alt="Background Maria Jose"
        ></img>
      </article>
      <section className="go">
        <Fade>
          <div className="go__header">
            <p>Web design</p>
            <img
              className="go__header-img"
              src={process.env.PUBLIC_URL + "/assets/images/projects/marca.png"}
              alt="Background Maria Jose"
            ></img>
          </div>
        </Fade>
        <section className="go__icons">
          <div className="icons">
            <a
              className="icons__home"
              target="_blank"
              href="https://www.behance.net/gallery/106946623/Go-Munch"
            >
              <img
                className="icons__home"
                src={process.env.PUBLIC_URL + "/assets/svg/behance.svg"}
                alt="Behance logo"
              ></img>
            </a>
          </div>

          <div className="icons">
            <a
              className="icons__home"
              target="_blank"
              href="https://github.com/ariasmjota/gomuch"
            >
              <img
                className="icons__home"
                src={process.env.PUBLIC_URL + "/assets/svg/git.svg"}
                alt="GitHub logo"
              ></img>
            </a>
          </div>

          <div className="icons">
            <a
              className="icons__home"
              target="pdf-frame"
              href="https://gomunch-87de6.web.app/"
            >
              <img
                className="icons__home"
                src={process.env.PUBLIC_URL + "/assets/svg/ww.svg"}
                alt="GitHub logo"
              ></img>
            </a>
          </div>
        </section>
        <Fade delay={100}>
          <div className="go__section">
            <img
              src={process.env.PUBLIC_URL + "/assets/images/projects/1.png"}
              alt="Background Maria Jose"
            ></img>
          </div>
        </Fade>
        <Fade>
          <div className="go__section">
            <img
              src={process.env.PUBLIC_URL + "/assets/images/projects/2.png"}
              alt="Background Maria Jose"
            ></img>
          </div>
        </Fade>
        <Fade>
          <div className="go__section">
            <img
              src={process.env.PUBLIC_URL + "/assets/images/projects/3.png"}
              alt="Background Maria Jose"
            ></img>
          </div>
        </Fade>
        <Fade>
          <div className="go__section">
            <img
              src={process.env.PUBLIC_URL + "/assets/images/projects/5.png"}
              alt="Background Maria Jose"
            ></img>
          </div>
        </Fade>
        <Fade>
          <div className="go__section">
            <img
              src={process.env.PUBLIC_URL + "/assets/images/projects/6.png"}
              alt="Background Maria Jose"
            ></img>
          </div>
        </Fade>

        <Fade>
          <div className="go__section">
            <img
              src={process.env.PUBLIC_URL + "/assets/images/projects/8.png"}
              alt="Background Maria Jose"
            ></img>
          </div>
        </Fade>
        <Fade>
          <div className="go__section">
            <img
              src={process.env.PUBLIC_URL + "/assets/images/projects/10.png"}
              alt="Background Maria Jose"
            ></img>
          </div>
        </Fade>
        <Fade>
          <div className="go__section">
            <img
              src={process.env.PUBLIC_URL + "/assets/images/projects/11.png"}
              alt="Background Maria Jose"
            ></img>
          </div>
        </Fade>
        <Fade>
          <div className="go__section">
            <img
              src={process.env.PUBLIC_URL + "/assets/images/projects/7.png"}
              alt="Background Maria Jose"
            ></img>
          </div>
        </Fade>
      </section>
    </div>
  );
};
